.skill{
    width: 100%;
    height: 8%;
    /* background-color: burlywood; */
    margin-top: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    
    

.first{
    width: 25%;
    height: 28%;
    background-color: aliceblue;
    border: 2px solid white;
    border-radius: 20px;

    text-align: center;
    

    &:hover{
    
        background-color:aqua;
        color: white;
        opacity: 1;
    }
    >h1{
        font-size: 70px;
        color: chocolate;
    }
    h3{
        font-size: 40px;
        /* font: 200; */
    }
    >span{
        color: red;
        font-size: 100px;
    }

   
}

}
span{
    color:darkgray;
    font-size: 20px;
}


@media only screen and (max-width: 450px) {

.skill{
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    

    >.first{
        width: 90%;
        height: 25%;
        margin-top: 20%;
        border: 1px solid aliceblue;
    }

}


}
