.rating{
    margin-top: 10%;
    width: 100%;
    height:30vh ;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    background-color: black;

    &:hover{
        opacity: 0.4;
    }
    
}


.love1{
    width: 25%;
    height:30vh;
    /* background-color: black; */
    /* color: red; */
    padding-top: 4%;
    text-align: center;
    font-size: 20px;
    color: white;
    letter-spacing: 5px;


}
@media only screen and (max-width: 450px) {

.rating{
    width: 100%;
    height: 100%;
    margin-top: 140%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: aqua;
    
    /* visibility: hidden; */

}
.love1{
    margin-left: 1%;
    width: 90%;
    align-items: center;
    color: black;

    /* visibility: hidden; */
    &:hover{
        opacity: 0.4;
    }
}


}



