*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    text-decoration: none;

}
.hamburger-menu{
    visibility: hidden;
}

.navbar{
    background-color: black;
    width: 100%;
    height: 100vh;
    opacity: 0.8;
}


nav{
    
    width: 100%;
    height: 10%;
    background-color: white;
    display: flex;
    justify-content:space-between ;
    padding: 1rem;
    /* top:0%; */
    text-align: center;
    position:fixed;
    color: black;
    font-family: sans-serif;
    font: 1000;
>h1{
    /* top:100%;s */
    margin-top: 1rem;
    margin-left: 10%;
    text-transform: uppercase;
    /* position: absolute; */
}

>main{
    width: 80%;
    display: flex;
    /* justify-content: flex-end; */
    font: 700;
    color: black;
    font-size: 18px;
    padding-right: 1;
    margin-left: 700px;

    >a{
        color: black;
        margin: 1rem;
        text-decoration: none;

     &:hover{
        color:chocolate;
        text-decoration: underline;
        
     }

    }
    
}


}
.header{
    /* visibility: hidden; */
    /* opacity: 0.1; */
    width: 100%;
    height: 50vh;
    /* padding-left: %; */
    /* top: 50%; */
    /* width: 100%; */
    /* background-color: black; */
    /* height: 100vh; */

    >h1{
        /* position: absolute; */
        top:50%;
        /* left: 50%; */
        font-size:80px;
        font: 2000;
        color:aliceblue;
        /* margin-top: 50%; */
        padding-top: 20%;
        margin-left: 25%;


    }
    
    >.side-img{
        width: 50%;
        height: 80%;
        position: absolute;
        top: 15%;
        margin-left: 40%;
        /* left: 60%; */
        opacity: 0.4;
        animation: imageUp 0.7s linear infinite;
    }
    >div{
   margin-left: 30%;
   display: flex;
   justify-content: space-between;
   width:10%;
   margin-top: 1rem;

    >a{
        text-decoration: none;
        color: aliceblue;
        /* flex-direction: row; */
        /* justify-content: space-between; */
        font: 800;
        font-size: 25px;
        /* justify-content:space-between; */
        margin-inline: 25px;
        /* text-align: center; */

   &:hover{
            color: brown;
        }
    }


}
}
@media only screen and (max-width: 450px) {


*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    text-decoration: none;

}
.hamburger-menu{
    visibility: visible;
    width:20%;
    font-size: 25px;
    height: 10%;
}
.main{
    text-align: left;
    width: 200%;
    height: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    >Link{
        position: absolute;
        background-color: black;
        visibility: visible;
        display: flexbox;
    }
}




 nav{
    display: flex;
    /* flex-direction: row; */
    /* justify-content:space-evenly; */
    /* align-items: center; */
    >h1{

        text-align: center;
        /* margin-left: 27%; */
        align-items: center;
    } 
    .hamburger-menu{
        margin-left: 50%;
        /* text-align: right; */
    }
}
.header{
    /* margin-top: 50%; */
    

    >h1{
        position: absolute;
        font-size: 30px;
        margin-top: -20%;
        margin-left: 10%;
    }

>.side-img{
    margin-top: 10%;
    height:75% ;
}
>div{
    visibility: hidden;
    position: absolute;
    margin-top: 105%;
    margin-left: 0%;
    margin-inline: 5px;  
    display: flex;
    flex-direction:column
    /* justify-content: space-between; */

}
>a{
    margin-inline: 5px;
}
}
}

